<template lang="">
  <div id="crud">
    <div class="login">
      <div class="pswd">
        <div class="input-group">
          <label for="input"></label>
          <input
            id="input"
            :type="inputType"
            v-model.trim="password"
            class="input"
            @keyup.enter="submit"
            placeholder="********"
          />
          <button class="input-group-icon" @click="togglePassword">
            {{ toggleBtnIcon }}
          </button>
        </div>
        <popupDialog 
          v-if="passwordIncorrect" 
          @close="closeModal"
          class="modal"
        >
          <template #body> 
            The password you entered is incorrect.
          </template>
        </popupDialog>
      </div>
    </div>
  </div>
</template>
<script>

import popupDialog from "../components/Dialog/popupDialog.vue";

export default {
  name: "LoginView",
  components: {
    popupDialog,
  },
  data() {
    return {
      showPassword: false,
      passwordIncorrect: false,
      password: "",
      secretPswd: process.env.VUE_APP_CRUD_PSWD,
    };
  },
  methods: {
    togglePassword() {
      this.showPassword = !this.showPassword;
    },
    submit() {
      if (this.password != this.secretPswd) {
        this.passwordIncorrect = true;
      } else {
        this.passwordIncorrect = false;
        localStorage.setItem('password', this.password);
        this.$router.push('/private');
        console.log('login');  
        // localStorage.removeItem('YourItem')
      }
      this.password = "";
    },
    closeModal() {
      this.passwordIncorrect = false;
    },
  },
  computed: {
    inputType() {
      return this.showPassword ? "text" : "password";
    },
    toggleBtnIcon() {
      return this.showPassword ? "🐵" : "🙈";
    },
  },
};
</script>
<style lang="scss" scoped>
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

#crud {
  .login {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .pswd {
      font-family: "Poppins", sans-serif;
      
      @media all and (max-width: 600px) {
        width: 300px;
      }

      .input-group {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #ffffff;
        border-radius: 0.5rem;
        overflow: hidden;
        font-size: 2rem;
        box-shadow: 0px 2px 4px 1px rgba(0, 0, 0, 0.25);
        @media all and (max-width: 600px) {
          font-size: 1.1rem;
        }

        input {
          border: 0;
          padding: 1rem;

          &:focus {
            outline: 0;
          }
        }

        .input-group-icon {
          padding: 0.5rem;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          align-self: stretch;
          border: 0;
          background-color: inherit;
          cursor: pointer;
        }
      }
    }
  }

  .loading {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: red;
  }
}


</style>
